<template>
	<div>
		<article>
			<h2 class="title_tj">
				<p>博主<span>推荐</span></p>
			</h2>
			<div class="bloglist left">
				<!--wz-->
				<div class="wz" v-for="item in listData">
					<h3>{{item.title}}</h3>
					<p class="dateview"><span>{{util.formatDate(item.dateline*1000)}}</span><span>作者：{{item.username}}</span><span>分类：[<a href="#">{{item.catid}}</a>]</span></p>
					<figure><img src="../lib/images/timg.jpg"></figure>
					<ul>
						<p v-for="i in 7">
							{{item.title}}
						</p>
						<a title="阅读全文" href="/" target="_blank" class="readmore">阅读全文>></a>
					</ul>
					<div class="clear"></div>
				</div>
				<!--end-->

			</div>
			<!--right-->
			<aside class="right">
				<div class="my">
					<h2>关于<span>博主</span></h2>
					<img src="../lib/images/timg.jpg" height="200" width="200" alt="博主"/>
					<ul>
						<li>博主：虫师</li>
						<li>职业:充满梦想的键盘工作者</li>
						<li>简介：对技术转化为利润具有强烈的欲望</li>
						<li></li>
					</ul>
				</div>
				<div class="bdsharebuttonbox"><a href="#" class="bds_more" data-cmd="more"></a><a href="#" class="bds_qzone" data-cmd="qzone" title="分享到QQ空间"></a><a href="#" class="bds_tsina" data-cmd="tsina" title="分享到新浪微博"></a><a

						href="#" class="bds_tqq" data-cmd="tqq" title="分享到腾讯微博"></a><a href="#" class="bds_renren" data-cmd="renren" title="分享到人人网"></a><a href="#" class="bds_weixin" data-cmd="weixin" title="分享到微信"></a></div>
				<div class="news">
					<h3 class="ph">
						<p>点击<span>排行</span></p>
					</h3>
					<ul class="paih" v-for="(item, k) in this.listData.slice(5, 10)">
						<li><a href="/" :title="item.title" target="_blank">{{item.title}}</a></li>
					</ul>
				

					<h3 class="links">
						<p>友情<span>链接</span></p>
					</h3>
					<ul class="website">
						<li><a href="#">个人博客</a></li>
					</ul>
				</div>
			</aside>
		</article>
	</div>
</template>

<script type="text/javascript">

	import Axios from 'axios'
	import util from "../webapp/js/util"
	import ApiConfig from "../webapp/js/api.config"
	import fetchJsonp from "fetch-jsonp"

	export default({
        data(){
			return {
                util:new util(),
                listData:[],
			}
		},
		methods:{
            getData(){

                this.$http.get(ApiConfig.get_news_api).then(response => {

                    // resolve to Blob
                    this.listData=response.body.result
                }).then(cnt => {

                });

            },
			// getData1(){
			//
            //     fetchJsonp(ApiConfig.get_news_api).then((response)=>{
            //             return response.json()
            //         }).then((json)=>{
            //         	this.listData=json.result;
            //     	}).catch((ex)=>{
            //         	console.log('parsing failed', ex)
            //     })
			// }
		},
        mounted(){  /*生命周期函数*/

            this.getData();

        }
	})

</script>



